.p-info-container {
}

.name-label {
    display: block;
    font-size: 20px;
}

.diff-btn-container{
    width: 90vw;
    height: 100px;

    display: flex;
    justify-content: space-between;

    margin: 1rem auto;
}

.diff-btn{
    height: 100px;
    width: 100px;

    background: none;
    border: none;

    display: flex;
    flex-direction: row;

    float: left;

    justify-content: center;
    align-items: center;
}

.accessibility-cont {
    display: flex;
    justify-content: center;
}

.accessibility-btn {
    background: linear-gradient(180deg, #B61918 0%, #EE3124 100%);
    border: none;
    border-radius: 30px;
    color: white;

    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;

    width: 90vw;

    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
}

.p-info-next {
    background: linear-gradient(180deg, #1488CA 0%, #005CA9 100%);
    border-radius: 100px 0 0 0 ;
    border: none;

    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;

    bottom: 0;
    right: 0;

    height: 75px;
    width: 130px;

    font-size: 20px;
    color: white;
}
