*{
  margin: 0;
  padding: 0;
  font-family: "Verlag", sans-serif;
}

html{
  font-size: 62.5%;	/* Default font size in browser is 16px.*/
}

h1, h2 {
 text-align: center;
}

h1{
  font-size: 48px;
  font-weight: 900;

  line-height: 140%;

  text-transform: uppercase;
}

h2{
  font-size: 24px;
  font-weight: bold;

  line-height: 100%;

  text-transform: uppercase;
}

h3{
  font-size: 22px;
  font-weight: 400;

  line-height: 24px;

  text-transform: capitalize;
}

h4{
  font-size: 20px;
  font-weight: 300;

  text-transform: capitalize;
}

p {
  font-size: 18px;
  font-weight: 400;
}

input{
  width: 80vw;
  height: fit-content;

  padding: 2rem 5vw;
  margin: 0.5rem auto;

  font-size: 16px;

  border-radius: 0;
  border: 1px solid black;
}

.grid-container{
  display: grid;
  position: fixed;
  grid-template-areas:
	"header"
	"main"
	"footer";
  grid-template-columns: 1fr;
  grid-template-rows: 10rem 1fr 25rem;
  height: 100%;
}

.container {
  width: 100vw;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.basic-btn{
  display: flex;

  width: fit-content;
  height: fit-content;

  padding: 2rem 3rem;
  margin: 0 auto;

  background: linear-gradient(180deg, rgba(0, 92, 169, 0.94) 0%, #1488CA 100%);
  border-radius: 50px;
  border: none;

  text-decoration: none;
}

.basic-btn h3{
  text-transform: uppercase;
  color: white;
}

.text-btn{
  display: flex;

  width: fit-content;
  height: fit-content;

  background: none;
  border: none;
  border-bottom: 1px solid black;

  text-decoration: none;

  margin: 0 auto;
}

.text-btn h4{
  color: black;
}

